import React from 'react'
import './style.css'

import * as logo from '../../../assets/Clean Walk/Loading_Screen/Search.logo.small.jpg.png'
import * as menu from '../../../assets/Clean Walk/Video/VID_next.png'

const IntroText = (props: any) => {

  return (

    <div className='loader_logoicon_containers'>
      <img className='loader_logoicon' src={logo} />
      <div className="introtext">
        Get ready to play, uncover secrets, learn from experts, and understand how our choices can lead to a radically different future!
      </div>
      <img onClick={
        () => {
          props.hidePopup(false)
        }
      } className='loader_Nexticon' src={menu} />
    </div>

  )
}

export default IntroText


